  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff"); }

html {
  height: 100%; }

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 5;
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1366px;
  box-sizing: border-box;
  padding: 35px 103px; }

.page-header__logo-image {
  fill: #f9fbfd; }

.page-header__toggle {
  position: absolute;
  top: 10px;
  right: 5px;
  display: none;
  width: 40px;
  height: 40px;
  border: none;
  background: transparent; }

@media (max-width: 1023px) {
  .page-header {
    padding: 30px 45px; }
  .page-header__logo-image {
    width: 38px;
    height: 20px; } }

@media (max-width: 767px) {
  .page-header {
    position: relative;
    flex-direction: column;
    padding: 20px 15px; }
    .page-header--menu-closed {
      position: absolute; }
    .page-header--menu-opened {
      position: relative; }
  .page-header__logo-image {
    width: 42px;
    height: 22px;
    fill: #011c40; }
  .page-header--menu-closed .page-header__logo-image {
    fill: #f9fbfd; }
  .page-header--menu-opened .page-header__toggle {
    display: block; }
    .page-header--menu-opened .page-header__toggle::before, .page-header--menu-opened .page-header__toggle::after {
      content: "";
      position: absolute;
      top: 19px;
      left: 13px;
      width: 18px;
      height: 2px;
      background-color: #011c40; }
    .page-header--menu-opened .page-header__toggle::before {
      transform: rotate(45deg);
      box-shadow: none; }
    .page-header--menu-opened .page-header__toggle::after {
      transform: rotate(-45deg); }
  .page-header--menu-closed .page-header__toggle {
    display: block; }
    .page-header--menu-closed .page-header__toggle::before {
      content: "";
      position: absolute;
      top: 13px;
      left: 6px;
      width: 25px;
      height: 2px;
      background-color: #f9fbfd;
      box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd; }
  .page-header--menu-closed .page-header__nav {
    display: none; } }

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-left: 80px;
  display: flex;
  flex-wrap: wrap; }
  .nav__list--footer {
    margin-left: 8px; }

.nav__item {
  margin-left: 33px;
  margin-top: 10px;
  margin-bottom: 10px; }

.nav__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd; }
  .nav__link:hover, .nav__link:focus {
    color: #86afd7; }

@media (max-width: 1023px) {
  .nav__list--footer {
    margin-left: 0; }
  .nav__item {
    margin-left: 24px;
    margin-top: 0; }
  .nav__link {
    font-size: 14px;
    line-height: 17px; }
    .nav__link--footer {
      font-size: 12px;
      line-height: 15px; } }

@media (max-width: 767px) {
  .nav__list {
    flex-direction: column;
    margin-left: 0;
    margin-top: 42px;
    margin-bottom: 20px; }
    .nav__list--footer {
      margin-top: 20px;
      margin-bottom: 13px; }
  .nav__item {
    margin-left: 0;
    margin-bottom: 21px; }
    .nav__item--order-change {
      order: -1; }
    .nav__item--footer {
      margin-bottom: 14px; }
  .nav__link--main-menu {
    font-size: 18px;
    line-height: 22px;
    color: #011c40; }
  .nav__link--main-menu:hover, .nav__link--main-menu:focus {
    color: #035ed7; } }

.page-main {
  margin: 0 auto;
  width: 100%;
  max-width: 1366px;
  min-width: 320px;
  flex-grow: 1; }

.page-main__wrapper-main-title-and-image {
  position: relative;
  overflow: hidden; }

.page-main__main-title-and-text {
  position: absolute;
  top: 234px;
  left: 198px;
  z-index: 1;
  padding-right: 20px; }

.page-main__main-title-and-text h1 {
  margin-bottom: 0;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  color: #f9fbfd; }

.page-main__main-title-and-text p {
  margin-top: 12px;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #f9fbfd; }

.page-main__wrapper-main-title-and-image img {
  margin-left: 50%;
  transform: translateX(-50%);
  display: block;
  max-width: none; }

@media (max-width: 1023px) {
  .page-main__main-title-and-text {
    left: 46px; }
  .page-main__main-title-and-text h1 {
    font-size: 50px;
    line-height: 61px; }
  .page-main__main-title-and-text p {
    font-size: 18px;
    line-height: 22px; }
  .page-main__wrapper-main-title-and-image img {
    transform: translateX(-50%) translateY(-12px); } }

@media (max-width: 767px) {
  .page-main__main-title-and-text {
    top: 153px;
    left: 18px; }
  .page-main__main-title-and-text h1 {
    font-size: 36px;
    line-height: 45px; }
  .page-main__main-title-and-text p {
    font-size: 16px; }
  .page-main__wrapper-main-title-and-image img {
    transform: translateX(-50%) translateY(-150px); } }

.page-footer {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1366px;
  min-width: 320px;
  box-sizing: border-box;
  padding: 50px 70px 47px 103px;
  background-color: #011c40; }

.page-footer__wrapper {
  display: flex; }

.page-footer__logo-image {
  fill: #f9fbfd; }

@media (max-width: 1090px) {
  .page-footer {
    padding-left: 40px;
    padding-right: 50px; } }

@media (max-width: 1023px) {
  .page-footer {
    padding: 48px 22px 43px 45px; }
  .page-footer__logo-image {
    width: 38px;
    height: 20px; } }

@media (max-width: 767px) {
  .page-footer {
    flex-direction: column;
    padding: 40px 15px 35px 15px; }
  .page-footer__wrapper {
    flex-direction: column; }
  .page-footer__logo-image {
    width: 42px;
    height: 22px; } }

.social {
  margin-left: 50px;
  display: flex;
  flex-wrap: wrap;
  align-items: center; }

.social__link {
  margin-right: 33px; }
  .social__link:hover .social__image,
  .social__link:focus .social__image {
    fill: #86afd7; }

.social__image {
  fill: #f9fbfd; }

@media (max-width: 1023px) {
  .social__link {
    margin-right: 23px; }
  .social__image--facebook {
    width: 9px;
    height: 16px; }
  .social__image--instagram {
    width: 16px;
    height: 16px; }
  .social__image--twitter {
    width: 16px;
    height: 13px; } }

@media (max-width: 767px) {
  .social {
    margin-left: 0; } }
